<template>
    <div>
        <mt-header fixed>
            <div v-on:click="" class="fs-25" slot="left">
                <mt-button class="icon-setup"></mt-button>
            </div>
        </mt-header>
        <div class="content bc-fff">

            <div class="userInfo c-fff">
                <div class="df userConent f-ac">
                    <div class="userHeader bc-img-c" v-bind:style="{backgroundImage:'url('+userData.userHeader+')'}"></div>
                    <div class="ml-5">
                        <div class="fs-16 fw-bold">{{userData.nickName}}</div>
                        <div class=""></div>
                    </div>
                </div>

            </div>
            <div class="p-10"></div>
            <div class="df fs-14 fc-303030 p-10 ">
                <div class="fg-2">我的订单</div>
                <div class="fg-2 tx-r fs-13 c-5F646E">查看全部></div>
            </div>
            <div class="df c-ff0036 tx-c pb-10 mt-5">
                <div class="fg-2" v-for="items in orderType" v-on:click="orderList(items.type)">
                    <div class="fs-25" v-bind:class="items.className"></div>
                    <div class="c-5F646E fs-12 mt-5">{{items.orderName}}</div>
                </div>
            </div>
            <div class="p-10"></div>
            <div class="df fs-14 fc-303030 p-10 ">
                <div class="fg-2">我的收藏</div>
            </div>
            <div class="df c-5F646E tx-c pb-10 mt-5">
                <div class="fg-2">
                    <div class="fs-20">1</div>
                    <div class="c-5F646E fs-12 mt-5">商品收藏</div>
                </div>
                <div class="fg-2">
                    <div class="fs-20">0</div>
                    <div class="c-5F646E fs-12 mt-5">店铺收藏</div>
                </div>
                <div class="fg-2">
                    <div class="fs-20">0</div>
                    <div class="c-5F646E fs-12 mt-5">内容收藏</div>
                </div>
                <div class="fg-2">
                    <div class="fs-20">0</div>
                    <div class="c-5F646E fs-12 mt-5">我的足迹</div>
                </div>
            </div>
        </div>
        <myFooter :current="3"></myFooter>
    </div>
</template>
<script>

import myFooter from "../../components/footer"
export default {
    name: "Main",
    components:{
        myFooter
    },
    data() {
        return {
            orderType: [
                {
                    orderName: "待付款",
                    className: "icon-card",
                    type: 0
                },
                {
                    orderName: "待发货",
                    className: "icon-yushu",
                    type: 1
                },
                {
                    orderName: "待收货",
                    className: "icon-fahuo",
                    type: 2
                },
                {
                    orderName: "待评价",
                    className: "icon-message2",
                    type: 3
                }
            ],

            userData: {
                userHeader: require("../../assets/userHeader.jpg"),
                nickName: "比比芭比波比"
            }
        };
    },
    methods: {
        orderList: function(type) {
            this.$router.push({
                path: "page/order/orderList",
                query: { type: type }
            });
        }
    }
};
</script>
<style scoped>
.userInfo {
    background-color: #ff0036;
    height: 80px;
    position: relative;
}
.userConent {
    position: absolute;
    bottom: -15px;
    left: 10px;
}
.userHeader {
    width: 70px;
    height: 70px;
    overflow: hidden;
    border-radius: 100%;
}
</style>




