import {
  Toast,
  MessageBox
} from 'mint-ui'
import jsonp from 'jsonp'
import Vue from 'vue'

import qs from 'qs'
var Indicator = require('../common/Indicator')
console.log(qs)
// let https = {
//   server: 'https://api.douban.com/v2'
// }
export default {
  openToast: function (message, position, duration) {
    if (!message || message === '') {
      message = ''
    }
    if (!position || position === '') {
      position = 'middle'
    }
    if (!duration || duration === '') {
      duration = 1000
    }
    var toast = Toast({
      message: message,
      position: position,
      duration: duration
    })
    return toast
  },
  openMessage: function (title, message, showCancelButton, cb) {
    MessageBox({
      title: title,
      message: message,
      showCancelButton: showCancelButton
    }).then(action => {
      if (action === 'confirm') {
        cb()
      }
    })
  },
  openAlert: function (message, title) {
    MessageBox.alert(message, title)
  },
  httpJsonp: function (url, data, successCb, errorCb) {
    Indicator.default.openIndicator()
    jsonp('https://api.douban.com' + url,
      data, (err, res) => {
        if (res) {
          Indicator.default.closeIndicator()
          successCb(res)
        }
        if (err) {
          Indicator.default.closeIndicator()
          errorCb(err)
        }
      }
    )
  },
  get: function (url, data, successCb, errorCb) {
    // $axios get请求
    Indicator.default.openIndicator()
    // 获取Main中设置的方法
    var $axios = Vue.prototype.$axios
    $axios.get(url, {
      params: data
    }).then(res => {
      Indicator.default.closeIndicator()
      successCb(res)
    }).catch(error => {
      Indicator.default.closeIndicator()
      if (errorCb) {
        errorCb(error)
      }
    })
  },
  post: function (url, data, successCb, errorCb) {
    // $axios post请求
    Indicator.default.openIndicator()
    var $axios = Vue.prototype.$axios
    $axios.post(url, qs.stringify(data)).then(function (response) {
      Indicator.default.closeIndicator()
    }).catch(function (error) {
      Indicator.default.closeIndicator()
      if (errorCb) {
        errorCb(error)
      }
    })
  },
  getCookie: function (name) {
    var strcookie = document.cookie
    var arrcookie = strcookie.split('; ')
    for (var i = 0; i < arrcookie.length; i++) {
      var arr = arrcookie[i].split('=')
      if (arr[0] === name) return unescape(arr[1])
    }
    return null
  },
  setCookie: function (name, value, expireHours) {
    var cookieString = name + '=' + escape(value) + '; path=/'
    if (expireHours > 0) {
      var date = new Date()
      date.setTime(date.getTime() + expireHours * 3600 * 1000)
      cookieString = cookieString + ';expires=' + date.toGMTString()
    }
    document.cookie = cookieString
  },
  delCookie: function (name) {
    var exp = new Date()
    exp.setTime(exp.getTime() - 1)
    var cval = this.setCookie(name, '')
    if (cval != null) document.cookie = name + '=' + cval + '; path=/;expires=' + exp.toGMTString()
  }
}



