<template>
    <div>

        <mt-header fixed title="商品详情">
            <div v-on:click="$router.back()" slot="left">
                <mt-button icon="back"></mt-button>
            </div>
            <mt-button icon="more" slot="right"></mt-button>
        </mt-header>

        <div class="content">

            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="items in swipeItems">
                        <!-- <h4>1111</h4> -->
                        <img :src="items.src" alt="">

                    </div>
                </div>
                <div class="swiper-pagination"></div>
            </div>
            <div class="productDetails">
                <div>
                    <span class="product_pice">￥{{goodsDetails.goods_price}}</span>
                    <span class="cuxiaojia">促销价</span>
                </div>
                <div class="fs-13 c-A2A2A2 mt-5">
                    <span>价格</span>
                    <del>￥{{goodsDetails.goods_market_price}}</del>
                </div>
                <div class="df fs-13 c-A2A2A2 mt-10 pb-10 porductType">
                    <div class="fg-1">快递费：7.00</div>
                    <div class="fg-1 tx-c">月销:{{goodsDetails.common_salenum}}</div>
                    <div class="fg-1 tx-r">江苏南京</div>
                </div>
                <div class="c-303030">
                    <div>
                        <span class="pt_label">天猫APP精选</span>
                    </div>
                    <div class="product_title">{{goodsDetails.common_name}}</div>
                </div>
            </div>

            <div class="productDetails mt-10 c-ff0036">
                <div class="df fw-bold">
                    <div class="icon-shop fs-20"></div>
                    <div class="fs-13 ml-5">三单有礼</div>
                </div>
                <div class="c-303030 fs-13 mt-5">
                    本商品参加天猫APP精选·三单有礼活动
                </div>
            </div>
            <div class="productDetails df mt-10 c-ff0036" v-on:click="show_parameter" style="display:none">
                <div class="c-303030 fs-13 fg-2">
                    产品参数
                </div>
                <div class="fg-1 c-303030 fs-13 tx-r">···</div>
            </div>
            <!-- 商品评论 -->
            <div class="productDetails mt-10">

                <div class="df">
                    <div class="fs-13 fg-1">商品评论({{productsReviewsNum}})</div>
                    <!-- goods_comment?shop_id=shop_id -->
                    <router-link v-show="isComment" :to="{path:'goods_comment',query:{goodsId:goodsId}}" class="fs-13 fg-1 c-ff0036 tx-r">查看更多></router-link>
                </div>
                <router-link to="goods_comment" class="products_reviews_type" style="display:none">
                    <span class="high_opinion">质量不错(64)</span>
                    <span class="high_opinion">快递不错(51)</span>
                    <span class="high_opinion">态度不错(50)</span>
                    <span class="high_opinion">是正品(40)</span>
                    <span class="high_opinion">性能好(6)</span>
                    <span class="negative">物流太慢(18)</span>
                </router-link>
                <!-- 评论的列表 -->
                <div class="products_reviews_content mt-10">
                    <productsReviewsList v-for="(items,index) in productsReviewsList" :nickname="items.user_name" :headerImg="items.user_grade_logo" :details="items.content"></productsReviewsList>
                </div>
            </div>

            <div class="shopDetile" v-html="goodsDetails.common_detail">
            </div>
            <footer class="c-fff">
                <div class="df f-ac">
                    <div class="fg-1 c-5F646E" v-on:click="openShop">
                        <div class="icon-shop-center fs-25"></div>
                        <div class="fg-1 fs-12">店铺</div>
                    </div>
                    <div id="Collection" class="fg-1 Collection" v-on:click="collection">
                        <div v-bind:class="{'c-ff0036':isCollection}">
                            <div class="icon-collection fs-25"></div>
                            <div class="fg-1 fs-12">收藏</div>
                        </div>

                    </div>
                    <div class="join_shopCar fg-2" v-on:click="addShopCar">加入购物车</div>
                    <div class="buy fg-2">立即购买</div>
                </div>

            </footer>
        </div>

        <!-- 商品参数弹出框 -->
        <mt-popup v-model="popupVisible" position="bottom" pop-transition="popup-fade" style="width:100%;">
            <div class="parameter">
                <div class="p-15-0">基本信息</div>
                <div class="df parameterType">
                    <div class="parameter_title">品牌</div>
                    <div class="parameter_Detalis">任天堂</div>
                </div>
                <div class="df parameterType">
                    <div class="parameter_title">任天堂型号</div>
                    <div class="parameter_Detalis">Nintendo Switch </div>
                </div>
                <div class="df parameterType">
                    <div class="parameter_title">版本类型</div>
                    <div class="parameter_Detalis">日版 欧版 港版 </div>
                </div>
                <div class="df parameterType">
                    <div class="parameter_title">颜色分类</div>
                    <div class="parameter_Detalis">红蓝手柄 灰色手柄 红蓝手柄+塞尔达传说荒野之息 红蓝手柄+马里奥奥德赛 红蓝手柄+异度之刃2 红蓝手柄+塞尔达传说+马里奥奥德赛 红蓝手柄+塞尔达+马里奥+异度之刃 红蓝手柄+塞尔达+赛车8 </div>
                </div>
                <div class="df parameterType">
                    <div class="parameter_title">内存容量</div>
                    <div class="parameter_Detalis">32GB </div>
                </div>
            </div>
        </mt-popup>
        <!-- 商品评论右侧弹出 -->

    </div>
</template>
<script>
//单独引用Swiper.min.js
import Swiper from "../../../node_modules/swiper/dist/js/swiper.min.js";
//商品详情列表的组件
import productsReviewsList from "../../components/products_reviews_list";
//商品评论的类型选择
import productReviewsType from "../../components/product_reviews_type";

export default {
    name: "product_details",
    components: {
        productsReviewsList,
        productReviewsType
    },
    data() {
        return {
            shop_id: "",
            goodsId: "",
            popupVisible: false,
            productsReviews: false,
            //控制评论列表的样式
            count: 0,
            //是否收藏的样式
            isCollection: false,
            isComment: false,
            swipeItems: [],
            goodsDetails: {},
            productsReviewsNum: 0,
            productsReviewsList: []
        };
    },
    mounted: function() {
        this.swiper();
        this.loadData();
        this.getGoodsNewReview();
    },
    methods: {
        //获取商品的详情；=
        loadData() {
            var _self = this;
            _self.common.get(
                "/index.php?ctl=Goods_Goods&met=goods&typ=json",
                {
                    goods_id: _self.$route.query.goodsId
                },
                function(data) {
                    if (data) {
                        if (data.status == 200) {
                            var data = data.data.data;
                            _self.goodsDetails = data.goods_info;
                            var goodsImage = [];
                            if (data.goods_image || data.goods_image != "") {
                                goodsImage = data.goods_image.split(",");
                            }
                            goodsImage.forEach(val => {
                                var obj = {
                                    src: val
                                };
                                _self.swipeItems.push(obj);
                            });
                            _self.goodsId = _self.$route.query.goodsId;
                            _self.shop_id = _self.goodsDetails.shop_id;
                        }
                    }
                },
                function(error) {}
            );
        },
        getGoodsNewReview() {
            var _self = this;
            _self.common.get(
                "/index.php?ctl=Goods_Goods&met=getGoodsNewReview&typ=json&sort=scores",
                {
                    goods_id: _self.$route.query.goodsId
                },
                function(data) {
                    if (data) {
                        if (data.status == 200) {
                            var items = data.data.data.goods_review_rows;
                            _self.productsReviewsNum = data.data.data.num;
                            if (data.data.data.num == 0) {
                                _self.isComment = false;
                            }
                            if (items.length > 0) {
                                _self.isComment = true;
                                items.forEach(key => {
                                    _self.productsReviewsList.push(key);
                                });
                            } else {
                            }
                        } else {
                        }
                    }
                }
            );
        },
        swiper() {
            var mySwiper = new Swiper(".swiper-container", {
                autoplay: true, //可选选项，自动滑动
                pagination: {
                    el: ".swiper-pagination",
                    type: "bullets"
                }
            });
        },
        show_parameter() {
            this.popupVisible = true;
        },
        show_productsReviews() {
            var _self = this;
            if (_self.productsReviews) {
                _self.productsReviews = false;
            } else {
                _self.productsReviews = true;
            }
        },
        collection() {
            //收藏的事件

            if (this.isCollection) {
                this.isCollection = false;
            } else {
                this.isCollection = true;
            }
        },
        addShopCar(){
            //加入购物车
            var _self = this;
            var userId = _self.common.getCookie("userId")
            if(!userId || userId == ""){
                _self.$router.push({
                    path:"../user/login"
                })
            }
        },
        openShop() {
            //打开店铺详情
            var _self = this;
            this.$router.push({
                path: "../shop/shop_details",
                query: {
                    shopId: _self.shop_id
                }
            });
        }
    }
};
</script>
<style scoped>
/* 单独引用Swiper.min.css */
@import url("../../../node_modules/swiper/dist/css/swiper.min.css");

>>> .shopDetile img {
    width: 100% !important;
}
.mint-header.is-fixed {
    z-index: 2;
}
.content {
    margin: 45px 0px 50px 0px;
}
.swiper-slide img {
    width: 100%;
}
.swiper-container-horizontal > .swiper-pagination-bullets[data-v-d81c94aa],
.swiper-pagination-custom[data-v-d81c94aa],
.swiper-pagination-fraction[data-v-d81c94aa] {
    font-weight: bold;
    color: #ff0036;
    font-size: 15px;
}
>>> .swiper-pagination-bullet-active{
    background: #ff0036 !important;
}
.productDetails {
    padding: 10px;
    background-color: #fff;
}
.product_pice {
    color: #ff0036;
    font-size: 18px;
    font-weight: 600;
}
.cuxiaojia {
    font-size: 10px;
    margin-left: 5px;
    color: #a2a2a2;
    background-color: #ebebeb;
}
.porductType {
    border-bottom: 1px solid #e6e6e6;
}
.product_title {
    font-size: 15px;
    line-height: 20px;
}
.pt_label {
    font-size: 10px;
    display: inline-block;
    padding: 0px 6px;
    margin: 8px 0px;
    background-color: #ff0036;
    color: #fff;
}
.buy {
    background-color: #ff0036;
    padding: 12px 0px;
}
.join_shopCar {
    background-color: #fe9402;
    padding: 12px 0px;
}
footer {
    position: fixed;
    width: 100%;
    bottom: 0px;
}
footer div {
    text-align: center;
    background-color: #fff;
}
.Collection {
    color: #5f646e;
}
.parameter {
    width: 100%;
    padding: 5px;
    font-size: 13px;
}
.parameterType {
    border-top: 1px solid #f4f4f4;
    padding: 15px 0px;
}
.parameter_title {
    width: 25%;
    color: #a2a2a2;
}
.parameter_Detalis {
    width: 75%;
    color: #a2a2a2;
}
.products_reviews_type {
    text-decoration: none;
}

.products_reviews_type span {
    display: inline-block;
    font-size: 11px;
    color: #666;
    padding: 8px;
    margin-top: 5px;
    border-radius: 10px;
}
/* 好评 */
.high_opinion {
    background-color: #fee;
}
/* 差评 */
.negative {
    background-color: #f5f5f5;
}
</style>



