<template>
    <div>
        <div class="df f-ac shopSearch fixed">
            <div v-on:click="$router.back()" class="mintui mintui-back c-fff ml-10"></div>
            <div class="fg-3 p-5">
                <input type="text" class="search_ipt" v-model="inputData.keywords" placeholder="在店铺中搜索">
                <!-- <mt-search placeholder="搜索">
                </mt-search> -->
            </div>
            <div class="search_txt" v-on:click="">
                <div>搜索</div>
            </div>
        </div>
        <div class="content">
            <div class="shopInfo df">
                <div class="shopbc" :style="shopBcImg">

                </div>
                <div class="shopInfoContent df">
                    <div class="shopLog">
                        <img src="../../assets/music.jpg" alt="">
                    </div>
                    <div class="shopName fg-1">adisapdmksa海外旗舰店</div>
                </div>

            </div>
            <div class="df tx-c shopNav">
                <div class="fg-1" v-for="(items,index) in shopNavArr" v-bind:class="{'shopIconActive':cruent == index}" v-on:click="shopNav(index)">
                    <div class="shopIcon" v-bind:class="items.className"></div>
                    <div class="shopNavText">{{items.test}}</div>
                </div>
            </div>
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="items in swipeItems">
                        <img :src="items.image" alt="">

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
//http://mall.jscntech.com//index.php?ctl=Shop&met=getStoreInfo&typ=json
import Swiper from "../../../node_modules/swiper/dist/js/swiper.min.js";
export default {
    name: "shop_details",
    data() {
        return {
            cruent: 0,
            shop_id: "",
            inputData: {
                keywords: ""
            },
            shopNavArr: [
                {
                    className: "icon-shopIcon",
                    test: "首页"
                },
                {
                    className: "icon-goodsIcon",
                    test: "商品"
                },
                {
                    className: "icon-upnewIcon",
                    test: "上新"
                },
                {
                    className: "icon-activeIcon",
                    test: "活动"
                }
            ],
            shopBcImg: {
                backgroundImage:
                    "url(" + require("../../assets/swipe.jpg") + ")"
            },
            swipeItems: [
                {
                    image: require("../../assets/swipe.jpg")
                },
                {
                    image: require("../../assets/swipe.jpg")
                }
            ]
        };
    },
    mounted() {
        this.loadData();
        this.swiper();
    },
    methods: {
        loadData() {
            var _self = this;
            _self.shop_id = this.$route.query.shopId;

            _self.common.post(
                "index.php?ctl=Shop&met=getStoreInfo&typ=json",
                {
                    k:"",
                    u:"",
                    shop_id: _self.shop_id
                },
                function(data) {
                    console.log(data);
                },
                function(error) {
                    console.log(error);
                }
            );
        },
        shopNav(index) {
            var _self = this;
            _self.cruent = index;
        },
        swiper() {
            var mySwiper = new Swiper(".swiper-container", {
                autoplay: true, //可选选项，自动滑动
                observer: true,
                observeParents: true
            });
        }
    }
};
</script>
<style scoped>
.swiper-container {
    margin-top: 5px;
}
.swiper-slide img {
    display: block;
    width: 100%;
}
.content {
    margin-top: 0px;
}
.shopInfo {
    padding: 90px 10px 10px 10px;
    position: relative;
}
.shopbc {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    opacity: 0.3;
}
.shopInfoContent {
    position: relative;
    bottom: 0px;
    left: 0px;
    width: 100%;
}
.shopLog {
    width: 50px;
    height: 50px;
}
.shopLog img {
    width: 100%;
    display: block;
}
.shopName {
    color: #303030;
    font-size: 15px;
    padding: 0px 10px;
}
.shopIcon {
    font-size: 23px;
    padding-top: 5px;
}
.shopIconActive {
    color: #da2623;
    border-bottom: 2px solid #da2623;
}
.shopNavText {
    font-size: 13px;
    padding: 5px 0px;
}
.shopNav {
    background-color: #fff;
    color: #717171;
}
.shopSearch {
    width: 100%;
    top: 0px;
    z-index: 9;
    background-color: #303030;
    background-color: rgba(0, 0, 0, 0.5);
}
.mint-search {
    height: auto !important;
}
.keyWordContent span {
    display: inline-block;
    padding: 5px 10px;
    margin: 5px;
    font-size: 13px;
    color: #303030;
    background-color: #fff;
    border: 1px solid #e2e2e2;
}
.search_ipt {
    width: 100%;
    border: none;
    padding: 8px 0px 8px 8px;
    border-radius: 10px;
}
.search_txt {
    color: #fff;
    font-size: 14px;
    text-align: center;
    padding: 0px 15px;
}
</style>




