<template>
    <div>
        <mt-header fixed title="登陆">
            <div v-on:click="$router.back()" slot="left">
                <mt-button icon="back"></mt-button>
            </div>
        </mt-header>
        <div class="content">
            <div class="logincontent">
                <div class="login_logo"><img src="../../assets/login_logo.png" alt=""></div>
                <div class="df"><input placeholder="手机号/邮箱/会员名" type="text"></div>
                <div class="df"><input placeholder="请输入密码" type="text"></div>

                <mt-button type="danger">LOGIN</mt-button>
            </div>

        </div>
    </div>
</template>
<script>
export default {
    name: "login"
};
</script>
<style scoped>
input {
    width: 100%;
    padding: 12px 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: none;
    font-size: 16px;
    border-radius: 10px;
}
.logincontent {
    width: 85%;
    margin: auto;
    padding-top: 20px;
}
.login_logo img {
    width: 25%;
    display: block;
    margin: 0 auto;
    padding-bottom: 15px;
}
.logincontent button{
    margin-top:20px;
    width:100%;
}
</style>



